                                <el-input placeholder="请输入电子邮箱" prefix-icon="el-icon-edit"
                                          style="width: 150px" size="mini" v-model="emp.email"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="联系地址：" prop="address">
                                <el-input placeholder="请输入联系地址" prefix-icon="el-icon-edit"
                                          style="width: 200px" size="mini" v-model="emp.address"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6">
