                                    <div slot="reference" style="width: 150px;display: inline-flex;font-size: 13px;border: 1px solid #C0C4CC;height: 26px
                                        ;border-radius:5px;color: #606266;cursor: pointer;align-items: center;padding-left: 8px;box-sizing: border-box"
                                         @click="showDepView">{{inputDep?inputDep:"请选择部门"}}
                                    </div>
                                </el-popover>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="电话号码：" prop="phone">
                                <el-input placeholder="电话号码" prefix-icon="el-icon-phone"
                                          style="width: 200px" size="mini" v-model="emp.phone"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6">
