                            <el-form-item label="学历:" prop="tiptopDegree">
                                <el-select v-model="emp.tiptopDegree" placeholder="最高学历" size="mini"
                                           style="width: 120px;">
                                    <el-option
                                            v-for="(item,index) in tiptopDegrees"
                                            :key="index"
