                                            :value="item">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="毕业院校：" prop="school">
                                <el-input placeholder="毕业院校名称..." prefix-icon="el-icon-edit"
                                          style="width: 150px" size="mini" v-model="emp.school"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="专业名称：" prop="specialty">
                                <el-input placeholder="专业名称" prefix-icon="el-icon-edit"
                                          style="width: 200px" size="mini" v-model="emp.specialty"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="入职日期:" prop="beginDate">
                                <el-date-picker
                                        v-model="emp.beginDate"
                                        type="date"
                                        placeholder="入职日期"
                                        value-format="yyyy-MM-dd"
                                        size="mini" style="width: 130px">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="5">
                            <el-form-item label="转正日期:" prop="conversionTime">
                                <el-date-picker
                                        v-model="emp.conversionTime"
                                        type="date"
                                        placeholder="转正日期"
                                        value-format="yyyy-MM-dd"
                                        size="mini" style="width: 125px">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="合同起始日期:" prop="beginContract">
                                <el-date-picker
                                        v-model="emp.beginContract"
                                        type="date"
                                        placeholder="合同起始日期"
                                        value-format="yyyy-MM-dd"
                                        size="mini" style="width: 130px">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="合同终止日期:" prop="endContract">
                                <el-date-picker
                                        v-model="emp.endContract"
                                        type="date"
                                        placeholder="合同终止日期"
                                        value-format="yyyy-MM-dd"
                                        size="mini" style="width: 180px">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="9">
                            <el-form-item label="身份证号码：" prop="idCard">
                                <el-input placeholder="请输入身份证号码" prefix-icon="el-icon-edit"
                                          style="width: 200px" size="mini" v-model="emp.idCard"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="聘用形式：" prop="engageForm">
                                <el-radio-group v-model="emp.engageForm">
                                    <el-radio label="劳动合同">劳动合同</el-radio>
                                    <el-radio label="劳务合同" style="margin-left: -10px">劳务合同</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="婚姻状况：" prop="wedlock">
                                <el-radio-group v-model="emp.wedlock">
                                    <el-radio label="已婚">已婚</el-radio>
                                    <el-radio label="未婚" style="margin-left: -10px">未婚</el-radio>
                                    <el-radio label="离异" style="margin-left: -10px">离异</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer" style="display: flex;justify-content: flex-end;margin-top: -40px">
    <el-button @click="centerDialogVisible = false" size="mini">取 消</el-button>
    <el-button type="primary" @click="buttonEvent" size="mini">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "EmpBasic",
        data() {
            return {
                emps: [],
                total: 0,
                loading: false,
                size: 10,
                page: 1,
                keywords: '',
                centerDialogVisible: false,
                politics: [],
                nations: [],
                positions: [],
                jobLevels: [],
                tiptopDegrees: ['小学', '初中', '高中', '大专', '本科', '硕士', '博士', '其他'],
                departmentDetail: [],
                visible: false,
                inputDep: '',
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                searchValue:{
                    politicId:1,
                    nationId:1,
                    jobLevelId:1,
                    posId:1,
                    departmentId:1,
                    engageForm:'劳动合同',
                    beginDateScope:null,
                },
                advanced:false,
                emp: {
                    id: '',
                    name: 'fy',
                    gender: '男',
                    birthday: '1995-08-09',
                    idCard: 610122199001031456,
                    wedlock: '未婚',
                    nationId: 1,
                    nativePlace: '湖北',
                    politicId: 3,
                    email: 'jiaxuming@qq.com',
                    phone: 18565558897,
                    address: '湖北省武汉市汉阳区',
                    departmentId: null,
                    jobLevelId: 9,
                    posId: 29,
                    engageForm: '劳动合同',
                    tiptopDegree: '本科',
                    specialty: '信息管理与信息系统',
                    school: '武汉大学',
                    beginDate: '2015-06-09',
                    workState: '在职',
                    workID: '',
                    contractTerm: 3,
                    conversionTime: '2018-04-01',
                    notWorkDate: '',
                    beginContract: '2018-01-02',
                    endContract: '2021-01-01',
                    workAge: '',
                },
                command: '',
                iconImg: 'el-icon-upload2',
                flag: false,
                advancedSearch:false,
                rules: {
                    name: [{required: true, message: "请输入姓名", trigger: 'blur'}],
                    gender: [{required: true, message: "请输入性别", trigger: 'blur'}],
                    birthday: [{required: true, message: "请输入出生日期", trigger: 'blur'}],
                    idCard: [{required: true, message: "请输入身份证号", trigger: 'blur'},
                        // {pattern:'',message: '身份证号格式不对'}
                    ],
                    wedlock: [{required: true, message: "请输入婚姻状况", trigger: 'blur'}],
                    nationId: [{required: true, message: "请输入民族", trigger: 'blur'}],
                    nativePlace: [{required: true, message: "请输入籍贯", trigger: 'blur'}],
                    politicId: [{required: true, message: "请输入政治面貌", trigger: 'blur'}],
                    email: [{required: true, message: "请输入邮箱", trigger: 'blur'}, {
