                        trigger: 'blur'
                    }],
                    phone: [{required: true, message: "请输入电话号码", trigger: 'blur'}],
                    address: [{required: true, message: "请输入地址", trigger: 'blur'}],
                    departmentId: [{required: true, message: "请输入部门", trigger: 'blur'}],
                    jobLevelId: [{required: true, message: "请输入职称", trigger: 'blur'}],
                    posId: [{required: true, message: "请输入职位", trigger: 'blur'}],
                    engageForm: [{required: true, message: "请输入聘用形式", trigger: 'blur'}],
                    tiptopDegree: [{required: true, message: "请输入学历", trigger: 'blur'}],
                    specialty: [{required: true, message: "请输入专业", trigger: 'blur'}],
                    school: [{required: true, message: "请输入毕业学校", trigger: 'blur'}],
                    beginDate: [{required: true, message: "请输入入职日期", trigger: 'blur'}],
                    workState: [{required: true, message: "请输入工作状态", trigger: 'blur'}],
                    wordID: [{required: true, message: "请输入工号", trigger: 'blur'}],
                    contractTerm: [{required: true, message: "请输入合同期限", trigger: 'blur'}],
                    conversionTime: [{required: true, message: "请输入转正日期", trigger: 'blur'}],
                    notworkDate: [{required: true, message: "请输入离职日期", trigger: 'blur'}],
                    beginContract: [{required: true, message: "请输入合同起始日期", trigger: 'blur'}],
                    endContract: [{required: true, message: "请输入合同终止日期", trigger: 'blur'}],
                    workAge: [{required: true, message: "请输入工龄", trigger: 'blur'}],
                }
            }
        },
        mounted() {
            this.initEmps()
        },
        methods: {
            cancelAdvancedSearch(){
                this.advancedSearch=false;
                this.advanced=false;
                this.searchValue={
                    politicId:1,
                        nationId:1,
                        jobLevelId:1,
                        posId:1,
                        departmentId:1,
                        engageForm:'劳动合同',
                        beginDateScope:null,
                }
            },
            doAdvanceSearch(){
                //发送搜索http请求
                this.getRequest("/employee/basic/?politicId="+this.searchValue.politicId+"&nationId="+this.searchValue.nationId
                +"&jobLevelId="+this.searchValue.jobLevelId+"&posId="+this.searchValue.posId+"&departmentId="+this.searchValue.departmentId
                +"&engageForm=" +this.searchValue.engageForm+"&beginDateScope="+this.searchValue.beginDateScope+"&page="+this.page+"&size="+this.size)
                    .then(resp=>{
                    if (resp){
                        this.emps=resp.data;
                        this.total=resp.total;
                        this.advancedSearch=false;
                        this.advanced=true;
                    }
                })
            },
            getSearchRequire(){
                this.getMaxWorkID();
                this.getJobLevels();
                this.getPositions();
                this.getNations();
                this.getPolitics();
                this.advancedSearch=!this.advancedSearch;
            },
            onProgress() {
                this.iconImg = 'el-icon-loading';
                this.flag = true;
            },
            onError() {
                this.iconImg = 'el-icon-upload2';
                this.flag = false;
            },
            onSuccess() {
                this.iconImg = 'el-icon-upload2';
                this.flag = false;
                this.initEmps()
            },
            exportEmps() {
                window.open('/employee/basic/export', '_parent');
            },
            showUpdateView(data) {
                this.emp = data;
                this.inputDep = data.department.name;
                this.getMaxWorkID();
                this.getJobLevels();
                this.getPositions();
                this.getNations();
                this.getPolitics();
                this.centerDialogVisible = true;
                this.command = 'update';
            },
            deleteEmpById(data) {
                this.$confirm('此操作将永久删除【' + data.name + '】员工, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/employee/basic/" + data.id).then(resp => {
                        if (resp) {
                            this.initEmps();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })
                })
            },
            buttonEvent() {
                if (this.command == 'update') {
                    this.putRequest("/employee/basic/", this.emp).then(resp => {
                        if (resp) {
                            this.inputDep = '';
                            this.centerDialogVisible = false;
                            this.initEmps();
                            this.command = '';
                        }
                    })
                }
                if (this.command == 'add') {
                    this.$refs['addEmp'].validate(valid => {
                        if (valid) {
                            this.postRequest("/employee/basic/", this.emp).then(resp => {
                                if (resp) {
                                    this.inputDep = '';
                                    this.centerDialogVisible = false;
                                    this.initEmps();
                                    this.command = '';
                                }
                            })
                        }
                    })
                }
            },
            handleNodeClick(data) {
                this.inputDep = data.name
                this.emp.departmentId = data.id;
                this.visible = !this.visible;
                this.searchValue.departmentId=data.id;
            },
            showAddEmpView() {
                this.getMaxWorkID();
                this.getJobLevels();
                this.getPositions();
                this.getNations();
                this.getPolitics();
                this.centerDialogVisible = true;
                this.command = 'add';
            },
            showDepView() {
                this.visible = !this.visible;
            },
            getMaxWorkID() {
                this.getRequest("/employee/basic/maxWorkId").then(resp => {
                    if (resp) {
                        this.emp.workID = resp.obj;
                    }
                })
            },
            getJobLevels() {
                this.getRequest("/employee/basic/joblevel").then(resp => {
                    if (resp) {
                        this.jobLevels = resp;
                    }
                })
            },
            getPositions() {
                this.getRequest("/employee/basic/position").then(resp => {
                    if (resp) {
                        this.positions = resp;
                    }
                })
            },
            getNations() {
                this.getRequest("/employee/basic/nation").then(resp => {
                    if (resp) {
                        this.nations = resp;
                    }
                })
            },
            getPolitics() {
                this.getRequest("/employee/basic/politicsstatus").then(resp => {
                    if (resp) {
                        this.politics = resp;
                    }
                })
            },
            doSearchEmp() {
                this.initEmps()
            },
            initEmps() {
                this.loading = true
                this.getRequest("/employee/basic/?page=" + this.page + '&size=' + this.size + '&name=' + this.keywords).then(resp => {
                    this.loading = false
                    if (resp) {
                        this.emps = resp.data;
                        this.total = resp.total;
                        this.advanced=false;
                    }
                })
                if (!window.sessionStorage.getItem("depView")) {
                    this.getRequest("/employee/basic/deps").then(resp => {
                        if (resp) {
                            this.departmentDetail = resp;
                            window.sessionStorage.setItem("depView", JSON.stringify(resp));
                        }
                    })
                } else {
                    this.departmentDetail = JSON.parse(window.sessionStorage.getItem("depView"));
                }
            },
            pageChange(currentPage) {
                this.page = currentPage;
                if(this.advanced){
                    this.doAdvanceSearch()
                }else{
                    this.initEmps()
                }
            },
            sizeChange(currentSize) {
                this.size = currentSize;
                if(this.advanced){
                    this.doAdvanceSearch()
                }else{
                    this.initEmps()
                }
            }
        }
    }
</script>

